import React from 'react'
import Rellax from "rellax";
import Navbar from "components/Navbars/ScrollTransparentNavbar.js";
import Header from 'pages/home/Header';
import About from 'pages/home/About';
import Companies from 'pages/home/Companies';
import Feature from 'pages/home/Feature';
import Footer from 'container/footer';
import Helmet from 'SEOHelmet/CustomHelmet';
import Services from 'pages/about/service'

function Index() {
  React.useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    
    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
  });
    React.useEffect(() => {
        document.body.classList.add("presentation-page");
        document.body.classList.add("sidebar-collapse");
        document.documentElement.classList.remove("nav-open");
        window.scrollTo(0, 0);
        document.body.scrollTop = 0;
    
        // initialise Rellax for this page
        if (window.innerWidth >= 991) {
          setTimeout(function () {
            new Rellax(".rellax", {
              center: true,
            });
          }, 5000);
          new Rellax(".rellax-header");
          new Rellax(".rellax-text");
        }
    
        // we need to add a script for the github buttons
        let script = document.createElement("script");
        script.src = "https://buttons.github.io/buttons.js";
        script.id = "github-buttons-script-id";
        document.body.appendChild(script);
    
        return function cleanup() {
          document.body.classList.remove("presentation-page");
          document.body.classList.remove("sidebar-collapse");
    
          // we need to remove the script when we change the page
          script.parentNode.removeChild(script);
        };
      });
    return (
        <>
        <Helmet title='Bek Mega Chilonzor || Home' content='Home Page'/>
           <Navbar/>
           <div className="wrapper">
           <Header/>
           <div className="main">
           <About/>
           <Services/>
           <Feature/>
           <Companies/>
           <Footer/>
           </div>
           </div>
        </>
    )
}

export default Index
